import styled from 'styled-components';

export const Button = styled.button`
    display: inline-block;
    border: none;
    font-size: 1.8rem;
    font-weight: bold;
    padding: 30px 60px;
    text-align: center;
    min-width: 140px;
    max-width: 240px;
    width: 100%;
`;



